import PageHeader from '../PageHeader/PageHeader';
import classes from './Skills.module.css';
import ulvi from './../../img/ulvi.jpg';

const listTitleStyle= { fontWeight:900, color:'#9b1fe8', marginBottom:'4px'}

const frontendSkills =
<ul>
  <li style={listTitleStyle}>Calender</li>
  <li> Pi Day</li>
  <li> Black Friday</li>
  <li> Octoberfest</li>
</ul>

const otherSkills =
<ul>
  <li style={listTitleStyle}>Share the Love</li>
  <li> Date Night</li>
  <li> Single's Night</li>
  <li> Traffic Light Party</li>
</ul>

const moreSkills =
<ul>
  <li style={listTitleStyle}>Others</li>
  <li>Decade Nights</li>
  <li>Fantasy Evenings</li>
  <li>Offbeat Holidays</li>
</ul>

const totalSkills = [frontendSkills, otherSkills, moreSkills]

const Skills =() => {
    return (
      <div className={classes.Skills} id='events'>
       <PageHeader title={'Events'}/>
       <p>
         We welcome you to our carefully and brilliantly planned events!
         Enjoy the first cocktail free, delicious food and great music by
         our in-house MC.
       </p>
       <div className={classes.Container}>

       <img className={classes.Ulvi} src={ulvi} alt= "Yummy food!" />
         
         {totalSkills.map(Skills => {
           return(
             <div className={classes.List}>
                {Skills}
             </div>
           )
         })}
       </div>
      </div>
    );
  }
  
  export default Skills;
  