import classes from './About.module.css';
import PageHeader from './../PageHeader/PageHeader';
import jennifer from './../../img/jennifer.jpg';
const About =() => {
    return (
      <div className={classes.About} id='about'>
      
      <PageHeader title={"About Us"}/>
      <div className={classes.Container}>
        <div className={classes.Text}>
          <h1>Hello, Welcome!!</h1>
          <p>
            Bella Vista Restaurants are well known,
            with a substantial gathering of people including families,  
            kids and seniors. We invite you to sitback,unwind and enjoy the lovely scenery
            while our gourmet expert sets you up a scrumptious dinner.
            Open day by day, Bella Vista offers a choice of naturally spiced dishes
            utilizing the best fixings accessible.
          </p>
        </div>

        <div className={classes.Photo}>
          <img className={classes.Binary} src={jennifer} alt=''/>
        </div>
      </div>

      </div>
    );
  }
  
  export default About;
  