import classes from './Projects.module.css';
import PageHeader from '../PageHeader/PageHeader';
import jennifer from './../../img/jennifer.jpg';
import mae from './../../img/mae.jpg';
import unsplash from './../../img/unsplash.jpg';
import louis from './../../img/louis.jpg';

const playButtonsvg =(url) =>{
    return(
        <div className={classes.playButton} onClick={(e) =>window.open(url, "blank").focus}>
            <h2> Our Delicacies</h2>

        </div>
    )
}

const photos =(url, thumbnail, description) =>{
    return(
        <div className={classes.photoContainer}>
            <div className={classes.Thumbnail}>
                {playButtonsvg(url)}
            <img src={unsplash} alt="gourmet"/>
            <img src={mae} alt="cocktails"/>
            <img src={jennifer} alt="noodles"/>
            <img src={louis} alt="noodles"/>
            
            
            </div>
            <div className={classes.Text}>
               <p>{description}</p>
            </div>
        </div>
    )
}

const mainMenuDescription = 'Our inhouse menu consists of pallettes made from different cultures, prepared  by ethnic chefs';


const Projects =() => {
    return(
        <div className={classes.Projects} id='trending'>
            <PageHeader title={"Services"}/>
            <div className={classes.Content}>
               <div className={classes.Paragraph}>
                  <p>
                     We offer a plethora of exclusive and inclusive outdoor and indoor activities such as rock climbing,
                     bungee jumping, cliff diving and miniature car racing.
                     Our expansive grounds provide breath taking sceneries of the Kyubei Montains and overlooking Lake Ero.
                     Instructors, trainors and life guards are positioned strategicall for customer convinience.
                     Visit us Today!!!
                  </p>
               </div>
               <div className={classes.Albums}>
                    {photos(mainMenuDescription)}
               </div>
            </div>
        </div>
    )
}


  
  export default Projects;
  