import classes from './Contact.module.css';
import {FaYoutube, FaInstagram, FaTwitter, FaLinkedin}  from "react-icons/fa";


const handleClick = (url) =>{
  return () =>window.open(url, '_blank')
}

const Contact =() => {
    return (
      <div className={classes.Contact} id="contact">
        <div className={classes.Context}>
           <FaYoutube color='white' size='30px' style={{padding:'1%'}}
           onClick={handleClick('')} />
           <FaInstagram color='white' size='30px' style={{padding:'1%'}}
           onClick={handleClick('')} />
           <FaTwitter color='white' size='30px' style={{padding:'1%'}}
           onClick={handleClick('')} />
           <FaLinkedin color='white' size='30px' style={{padding:'1%'}}
           onClick={handleClick('')} />
        </div>
        <p>&copy; 2021</p>
      </div>
    );
  }
  
  export default Contact;
  