import classes from './Home.module.css';
import louis from './../../img/louis-hansel.jpg';

const Home =() => {
    return (
      <div className={classes.Home}>
      
         <img className={classes.Louis} src={louis} alt= "Yummy food!" />
        
         <div className={classes.Container}>
           <h1 className={classes.Hello}>BELLA VI`STA</h1>
           <h1>A Taste Of The Good Life</h1>
           <p>Family Comes First!</p>


         </div>
      </div>
    );
  }
  
  export default Home;
  